.background-image {
    height: 100vh;
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),  url(/BBook-Frontend/static/media/wibubg.96a66652.jpg);
    background-repeat: repeat-x;
    animation: slideleft 10000s infinite linear;
    /* filter: blur(2px); */
    -webkit-animation: slideleft 10000s infinite linear;
    
