.search {
margin: 5px;
}

.search > h3 {
font-weight: normal;
}

.search > h1,
.search > h3 {
color: white;
margin-bottom: 15px;
text-shadow: 0 1px #0091c2;
}

.search > div {
display: inline-block;
position: relative;
}
.search{
    margin-top:10px;
}
.search > div:after {
content: "";
background: white;
width: 4px;
height: 10px;
position: absolute;
top: 33px;
right: 0px;
margin-right:20px;
transform: rotate(135deg);
}

.search > div > input {
color: white;
font-size: 16px;
background: transparent;
width: 10px;
height: 10px;
padding: 5px;
margin-top: 10px;
margin-right:20px;
border: solid 3px white;
outline: none;
border-radius: 35px;
box-shadow: 0 1px #0091c2;
transition: width 0.5s;
}

.search > div > input::-webkit-input-placeholder {
color: #efefef;
opacity: 1;
}

.search > div > input:-ms-input-placeholder {
color: #efefef;
opacity: 1;
}

.search > div > input::-ms-input-placeholder {
color: #efefef;
opacity: 1;
}

.search > div > input::placeholder {
color: #efefef;
opacity: 1;
}

.search > div > input::-ms-placeholder {
color: #efefef;
}

.search > div > input::-ms-input-placeholder {
color: #efefef;
}

.search > div > input:focus,
.search > div > input:valid {
width: 250px;
}

.dropbtn {
