    font-family: inherit;
    font-size: 100%;
    line-height: 1.15;
    margin-top: 10px;    
}
.listBook{
    width: 100%;
    position: absolute;
    bottom: -300px;

}
.groupInput .btn-expel{
    border-right: none;
    border-radius: 4px 0px 0px 4px;
    cursor: pointer;
    width: 30px;
    background-color: rgb(255, 255, 255);
    border-width: 1px;
    border-style: solid;
    border-color: rgb(236, 236, 236);
    border-image: none;
    border-image: initial;
    height: 30px;
    color: rgb(36, 36, 36);
    font-size: 14px;
    text-align: center;
    outline: none;
    transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s;
    -webkit-appearance: button;
}

.groupInput .btn-add{
    border-right: none;
    border-radius: 0px 4px 4px 0px;
    cursor: pointer;
    width: 30px;
    background-color: rgb(255, 255, 255);
    border-width: 1px;
    border-style: solid;
    border-color: rgb(236, 236, 236);
    border-image: none;
    border-image: initial;
    height: 30px;
    color: rgb(36, 36, 36);
    font-size: 14px;
    text-align: center;
    outline: none;
    transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s;
    -webkit-appearance: button;
}

.groupInput input{
    -webkit-writing-mode: horizontal-tb !important;
    text-rendering: auto;
    color: -internal-light-dark-color(black, white);
    letter-spacing: normal;
    word-spacing: normal;
    text-shadow: none;
    display: inline-block;
    -webkit-appearance: textfield;
    background-color: -internal-light-dark-color(rgb(49, 30, 30), rgb(59, 59, 59));
    -webkit-rtl-ordering: logical;
    cursor: text;
    margin: 0em;
    font: 400 13.3333px Arial;
    padding: 1px 2px;
    border-width: 2px;
    border-style: inset;
    border-color: -internal-light-dark-color(rgb(118, 118, 118), rgb(195, 195, 195));
    border-image: none;
    border-image: initial;
    width: 40px;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(236, 236, 236);
    border-image: initial;
    height: 26px;
    color: rgb(36, 36, 36);
    font-size: 14px;
    text-align: center;
    outline: none;
    transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s;
}
