/*
 * PopupForgotPassword css File
*/

.PopupForgotPassword {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
}

.PopupForgotPassword-content{
    position: absolute;
    top: 20%;
    left: 34%;
    width: 30%;
    padding: 20px;
    border-radius: 10px;
    background-color: white;
}

.close {
    color: black;
    float: right;
}

.close:hover {
    color: cyan;
    cursor: pointer;
}

.input-email{
    margin:15px 0;
	padding:15px 10px;
	width:100%;
	outline:none;
	border:1px solid #bbb;
	border-radius:20px;
	display:inline-block;
	-webkit-box-sizing:border-box;
	-moz-box-sizing:border-box;
		box-sizing:border-box;
	-webkit-transition:0.2s ease all;
	-moz-transition:0.2s ease all;
	-ms-transition:0.2s ease all;
		-o-transition:0.2s ease all;
		transition:0.2s ease all;
}