@import url(https://fonts.googleapis.com/css?family=Lily+Script+One);

.App {
    height: 100%;
    overflow: hidden;
}
@keyframes slideleft {
    from {
        background-position: 0%;
    }
    to {
        background-position: 90000%;
    }
}

@-webkit-keyframes slideleft {
from {
    background-position: 0%;
}
to {
    background-position: 90000%;
}
}
/* .image-background{
    height: 100vh;
    
} */

.background-image {
    height: 100vh;
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),  url(../../img/wibubg.jpg);
    background-repeat: repeat-x;
    animation: slideleft 10000s infinite linear;
    /* filter: blur(2px); */
    -webkit-animation: slideleft 10000s infinite linear;
    
    position: relative;
    background-size: cover;
    -webkit-background-size:cover;
    -moz-background-size:cover; 
    background-attachment: fixed;
}

.background-content {
    position: absolute;
    top: 0%;
    width: 100%;
    height: 100%;
}

.container {
    display: flex;
    text-align: center;
    position: absolute; 
    top: 68px;
    width: 100%;
    height: 91%;
    overflow: auto;
}

.text-white {
    color: white;
}

.cursor-pointer{
    cursor: pointer;
}